<script>
export default {
  name: "",
  props: {},
  components: {},
  data() {
    return {
      // 
      status: "",
      // 
      drawerVisible: false
    };
  },
  computed: {
    stateYear() {
      return this.$store.state.status;
    }
  },
  watch: {
    stateYear: {
      handler(val) {
        this.$nextTick(() => {
          this.status = val;
        });
      },
      immediate: true
    }
  },
  methods: {
    go_login(login) {
      this.$router.push({
        path: login
      });
      if (this.status == "") {
        // this.delCookie("sessionid");
        // this.delCookie("token");
        window.sessionStorage.clear();
        this.$router.push("/login");
        setTimeout(function() {
          window.location.reload();
        }, 600);
      }
    },
    changeTitle() {
      if (this.status == "") {
        this.status = "";
      }
    },
    changeTitle2() {
      if (this.status == "") {
        this.status = "";
      }
    },
    /**
     * @description: 
     * @param {*}
     * @return {*}
     */
    showDrawer() {
      this.drawerVisible = true;
    },
    // 
    go_route(pathstr) {
      if (this.status == "") {
        console.log(pathstr);
        console.log(this.$router);
        this.$router.push({
          path: pathstr
        });
        this.drawerVisible = false;
      } else {
        alert("");
        this.drawerVisible = false;
      }
      // this.$router.push({
      //   path: pathstr
      // });
      this.title_show = false;
    }
  },
  created() {},
  mounted() {}
};
</script>
<style scoped>
#phone-nav {
