// 
export function getPersonList() {
  return instance.get("/server/personnel_management/personnel_data/");
}
// 
export function addPersonInfo(formData) {
  return instance.post("/server/personnel_management/personnel_add/", formData);
}

// 
export function editPerson(formData) {
  return instance.post(
    "/server/personnel_management/personnel_edit/",
    formData
  );
}

// 
export function removePerson(id) {
  return instance.get("/server/personnel_management/personnel_del/", {
    params: {
      personnel_id: id
    }
  });
}

