export function queryInfo(info) {
  return instance.get("/server/personnel_management/select_initiate_job/", {
    params: {
      receive_department: info.checkedDpm,
      receive_person: info.checkedStaff,
      job_type: info.checkedType,
      out_time: info.checkedDate,
      page: info.page
    }
  });
}

